export interface FetchData {
    name: string
    datas: any[]
}

export interface Category {
    name: string
    items: Item[]
}

export interface Banner {
    image: string
    description: string
    itemCode: string
    item?: Item
}

export interface System {
    companyName: string
    companyAddress: string
    companyContact: string
    companyDescription: string
    serviceCharge10Percent: boolean
    tax6Percent: boolean
}

export interface Item {
    categoryName: string
    image: string
    code: string
    name: string
    description: string
    note: string
    choice?: string // come from sheet and use for app, then remove
    price?: string // come from sheet and use for app, then remove
    addOn?: string // come from sheet and use for app, then remove
    choices: Choice[]
    addOnGroups: AddOnGroup[]
    orderList: OrderChoice[]
    orderHistoryItemPrices: Choice[]
}

export interface RefItem {
    image: string
    code: string
    name: string
    description: string
    note: string
}

export interface Choice {
    name: string
    price: number
}

export interface AddOnGroup {
    name: string
    choice?: string // come from sheet and use for app, then remove
    price?: string // come from sheet and use for app, then remove
    addOns: AddOn[]
    noCharges: boolean
    minimumSelect?: number
    maximumSelect?: number
    defaultSelect?: number
    duplicateSelect: boolean
}

export interface AddOn {
    name: string
    price: number
}

export interface OrderChoice {
    itemCode: string
    name: string
    unitPrice: number
    qty: number
    total: number
    addOnGroups: OrderAddOnGroup[]
    overallTotal: number
}

export interface OrderAddOnGroup {
    name: string
    addOns: OrderAddOn[]
}

export interface OrderAddOn {
    name: string
    unitPrice: number
    qty: number
    total: number
}


export interface Order {
    orderNo?: number
    status: OrderStatus
    tableNo: number
    itemPrices: string | Choice[]
    amount: number
    createdDate: string
    deviceId: number
    note: string
}

export interface LSCustomerInfo {
    deviceId: number
}

export interface Sheet {
    name: string
    datas: any[]
}

export enum OrderStatus {
    waiting = 'waiting',
    queueing = 'queueing',
    preparing = 'preparing',
    completed = 'completed',
    paid = 'paid',
    cancelled = 'cancelled',
}

export enum CRUD {
    create = 'create',
    createWithInsert = 'createWithInsert',
    reading = 'reading',
    update = 'update',
    delete = 'delete',
}

export enum PostOrUpdate {
    post = 'post',
    update = 'update',
}

export enum ImageSize {
    'w75' = 's',
    'w100' = 't',
    'w150' = 'q',
    'w240' = 'm',
    'w320' = 'n',
    'w400' = 'w',
    'default' = '',
    'w640' = 'z',
    'w800' = 'c',
    'w1024' = 'b',
    'w1600' = 'h',
}

