
import { ImageSize, Item } from "@/interface";
import { convertFlickrImage } from "@/mixins/generalMixin";
import { defineComponent } from "vue";
import ActionButtonComponent from "../components/ActionButtonComponent.vue";
import ItemComponent from "../components/ItemComponent.vue";

// Import Swiper
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";

export default defineComponent({
  props: ["systemData", "banners", "categories"],
  emits: ["showItemDetail"],
  mounted() {
    window.document.title = `${this.systemData.companyName} - Menu Panda`;
  },
  components: {
    ActionButtonComponent,
    ItemComponent,
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      ImageSize: ImageSize,
    };
  },
  methods: {
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
    toPages(items: Item[]) {
      let results: Item[][] = [];
      let slot = null;
      for (let i = 0; i < items.length; i++) {
        if (i % 3 === 0) {
          results.push([]);
          slot = Math.floor(i / 3);
        }
        results[slot!].push(items[i]);
      }
      return results;
    },
    showItemDetail(code: string) {
      this.$emit("showItemDetail", code);
    },
  },
});
