
import { Choice, Item, OrderChoice } from "@/interface";
import { arrayMin, arraySum } from "@/mixins/generalMixin";
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    item: {
      type: Object as PropType<Item>,
      required: true,
    },
    fontSize: {
      type: Number,
    },
  },
  emits: ["showItemDetail"],
  computed: {
    fontSizeStyle() {
      return this.fontSize !== undefined
        ? { fontSize: `${this.fontSize}px` }
        : undefined;
    },
    choiceQty() {
      return arraySum(this.item.orderList, "qty");
    },
  },
  methods: {
    pushOrUpdateOrderChoiceQty() {
      if (this.item.orderList.length === 0) {
        const obj: OrderChoice = {
          itemCode: this.item.code,
          name: this.item.choices[0].name,
          unitPrice: this.item.choices[0].price,
          qty: 1,
          total: 0, // 这里不负责钱
          addOnGroups: [],
          overallTotal: 0,
        };
        this.item.orderList.push(obj);
      } else {
        this.item.orderList[0].qty++;
      }
    },
    showItemDetail(code: string) {
      this.$emit("showItemDetail", code);
    },
    getMinPrice(choices: Choice[]) {
      return arrayMin(choices, "price");
    },
  },
});
