        dataAddOns = dataAddOns.map(
          (addOnGroup: AddOnGroup, dataAddOnListindex: number) => {
            const obj: AddOnGroup = {
              name: addOnGroup.name,
              addOns: [],
              noCharges: false,
              minimumSelect: addOnGroup.minimumSelect,
              maximumSelect: addOnGroup.maximumSelect,
              defaultSelect: addOnGroup.defaultSelect,
              duplicateSelect: addOnGroup.duplicateSelect,
