            addOnGroupChoices.forEach((choice: string, index: number) => {
              obj.addOns.push({
                name: choice,
                price: addOnGroupPrices[index] ?? 0,
              } as AddOn);
            });

            obj.noCharges = obj.addOns.every(
              (addOn: AddOn) => addOn.price === 0
            );

            return obj;
