
import {
  AddOnGroup,
  ImageSize,
  Item,
  OrderAddOnGroup,
  OrderChoice,
} from "@/interface";
import {
  arrayFind,
  arrayFindIndex,
  arraySum,
  convertFlickrImage,
} from "@/mixins/generalMixin";
import { defineComponent, PropType } from "vue";
import ActionButtonComponent from "../components/ActionButtonComponent.vue";

export default defineComponent({
  props: {
    useFor: {
      type: String,
      required: true,
      default: "item",
    },
    item: {
      type: Object as PropType<Item>,
      required: true,
    },
  },
  emits: ["showItemDetail"],
  components: {
    ActionButtonComponent,
  },
  data() {
    return {
      ImageSize: ImageSize,
    };
  },
  computed: {
    choiceQty() {
      return arraySum(this.item.orderList, "qty");
    },
  },
  methods: {
    convertFlickrImage(imageLink: string, size: ImageSize | null) {
      return convertFlickrImage(imageLink, size);
    },
    showItemDetail(code: string) {
      this.$emit("showItemDetail", code);
    },
    removeOrderListItem(orderChoice?: OrderChoice) {
      if (confirm("Remove?")) {
        if (orderChoice !== undefined) {
          const findIndex = arrayFindIndex<OrderChoice>(
            this.item.orderList,
            "itemCode",
            orderChoice.itemCode
          );
          this.item.orderList.splice(findIndex, 1);
        } else {
          this.item.orderList = [];
        }
      }
    },
    updateOrderChoiceQty(orderChoice: OrderChoice, qty: number) {
      if (orderChoice.qty === 1 && qty === -1) {
        this.removeOrderListItem(orderChoice);
      } else {
        orderChoice.qty += qty;
      }
    },
    getAddOnConfigDuplicateSelectByAddOnGroup(addOnGroup: OrderAddOnGroup) {
      return arrayFind<AddOnGroup>(
        this.item.addOnGroups,
        "name",
        addOnGroup.name
      )!.duplicateSelect;
    },
  },
});
